<template lang="jade">

div
	div
		imagine-x(v-if="mode==='imagine'")
	div
		visualize-votes(v-if="mode==='votes'")
	div
		nominate(v-if="mode==='nominate'")


</template>

<script>
import store from '../vuex/store'
import attackAds from './attackAds'
import imagineX from './imagineX'
import visualizeVotes from './visualizeVotes'
import nominate from './nominate'

export default {
  beforeMount() {
    this.$store.dispatch("SET_MODE_BY_URL", window.location.pathname)
  },
  methods: {
      changeMode() {
        this.$store.dispatch(`CHANGE_MODE`)
      }
    },
    computed: {
      mode() {
        return this.$store.state.director.mode
      },
    },
    components: {
      attackAds,
      imagineX,
      visualizeVotes,
			nominate,
    },
    store,
    computed: {
      mode() {
        return this.$store.state.director.mode
      }
    },
}
</script>


<style lang="stylus">
  body
    width: initial;
    margin: 0;
    padding-bottom: 5rem;
    font-family: 'Arial', san-serif;


  #modeswitcher
    z-index: 1e10
    position:fixed
    top:0
    left:0
    cursor:pointer

</style>



