<template lang="jade">

#nominate
	main.container
		heading(:content='headingContent')
		variable-body(:content='variableContent')
		nomination
		footer-x

</template>

<script>
import VariableBody from '../generics/VariableBody.vue'
import Heading from '../generics/Heading.vue'
import FooterX from '../generics/FooterX.vue'

import Nomination from './Nomination'

let headingContent = ['Nominate an Independent']
let variableContent = [
	'Do you know someone you trust? Someone competent and hard-working, who has a deep understanding of one or more areas of provincial activity. Someone who understands the situation in health, in education, in social services, in energy, in housing... and has the will to find and inact solutions.',
	'It\'s not just saying no to the status quo, it\'s filling the legislature with motivated and intellegent people who will actually acknowledge and attempt to address the major structural changes needed in nearly every aspect of provincial jurisdiction.',
	'Do you know someone who you think should run as an independent? Then let us know and we will reach out and encourage them. Nominate them today:'
]

export default {
	data() {
		return {
			headingContent,
			variableContent,
		}
	},
	computed: {
			idea() {
				return this.$store.state.idea.content
			},
		},
		components: {
			Nomination,
			VariableBody,
			Heading,
			FooterX,
		},
		beforeMount() {
			this.$store.dispatch("SET_IMAGINE_X_BY_URL", window.location.pathname)
		}
}
</script>

<style lang="stylus" scoped>
  @import "../../styles/main"

	main
    font-family: Arial, san-serif;

  @media (min-width: breakpoint) {
    .hr--hide-lg {
        display: none;
    }
  }
</style>



