<template>
<nav class="navigation navigation--sidebar">
  <ul class="list--clean">
    <li >
      <a class="is-active" href="/">HOME</a>
    </li>
    <li>
      <a @click.prevent='changeMode("votes")' href="/votes">MLA VOTING RECORDS APP</a>
    </li>
    <li>
      <a @click.prevent='changeMode("nominate")' href="/votes">NOMINATE A CANDIDATE</a>
    </li>
  </ul>
</nav>
</template>

<script>
export default {
  methods: {
    changeMode(mode) {
      this.$store.dispatch(`CHANGE_MODE`, mode)
    },
  }
}
</script>

<style lang="stylus" scoped>
@import "../../styles/main"

.navigation {
    li {
        border-bottom: 1px solid lightgray;
        margin-bottom: 0;
        padding: 0.65rem 0;
    }
    a {
        text-decoration: none;
    }
    a.is-active {
        font-weight: bold;
    }
}

.navigation--offcanvas {
    li {
        padding: 0.65rem 2rem;
    }
}
.navigation--offcanvas.is-open {
    display: block;
}
.navigation--sidebar {
    display: none;
}

@media (min-width: breakpoint) {
    .navigation--sidebar {
        display: block;
    }
}

</style>



